import React from 'react';
import Card from 'react-bootstrap/Card';
import CardGroup from 'react-bootstrap/CardGroup';
import './ProjectList.css';
import { PROJECT_DATA } from './ProjectData';

export const Project = ({ projectImage, title, bodyText, projectUrl }) => {
    return (
        <Card style={{ width: "300px", margin: "15px", borderRadius: "10px", border: "1px solid gray" }}>
            <Card.Img
                variant="top"
                src={projectImage}
                style={{ borderBottom: "1px solid gray", maxHeight: "400px" }}
            />
            <Card.Body>
                <Card.Title>
                    <h3 className="text-center mt-3">{title}</h3>
                </Card.Title>
                <Card.Text className="text-center">
                    <a target="_blank" href={projectUrl} className="viewProjectBtn">
                        View
                    </a>
                </Card.Text>
            </Card.Body>
        </Card>
    );
}

export const ProjectList = () => {
    return (
        <div className="container-fluid">
            <div className="row" id="projects">
                <div className="col-md-12">
                    <h1 className="text-center">Project Gallery</h1>
                    <br />
                    <CardGroup className="center-content" id="projectCardGroup">
                        {
                            PROJECT_DATA.map(project =>
                                <Project
                                    projectImage={project.projectImage}
                                    title={project.title}
                                    bodyText={project.body}
                                    projectUrl={project.projectUrl}
                                />
                            )
                        }
                    </CardGroup>
                </div>
            </div>
        </div>
    )
}